import { Scene } from 'phaser';

export class MuseRunnerGame extends Scene {
    private character!: Phaser.GameObjects.Rectangle;
    private platforms!: Phaser.GameObjects.Group;
    private gameKeys!: { [key: string]: Phaser.Input.Keyboard.Key };
    
    // Game state
    private isGameRunning: boolean = false;
    private score: number = 0;
    private streak: number = 0;
    private characterSpeed: number = 120;
    private scoreText!: Phaser.GameObjects.Text;
    private streakText!: Phaser.GameObjects.Text;

    // Musical note frequencies (C major scale)
    private noteFrequencies = {
        'S': 261.63, // C4
        'D': 293.66, // D4
        'F': 329.63, // E4
        'G': 349.23, // F4
        'H': 392.00, // G4
        'J': 440.00, // A4
        'K': 493.88  // B4
    };

    constructor() {
        super('MuseRunnerGame');
    }

    create(): void {
        const { width, height } = this.cameras.main;

        // Create character (blue rectangle)
        this.character = this.add.rectangle(100, height - 100, 30, 40, 0x3498db);
        this.physics.add.existing(this.character);
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        characterBody.setCollideWorldBounds(true);
        characterBody.setBounce(0.1);

        // Create platforms group
        this.platforms = this.add.group();

        // Create initial platform
        const initialPlatform = this.add.rectangle(50, height - 50, 200, 20, 0x2ecc71);
        this.physics.add.existing(initialPlatform, true);
        this.platforms.add(initialPlatform);

        // Set up collisions
        this.physics.add.collider(this.character, this.platforms);

        // Musical keys setup (S-D-F-G-H-J-K)
        this.gameKeys = {
            'S': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.S),
            'D': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.D),
            'F': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.F),
            'G': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.G),
            'H': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.H),
            'J': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.J),
            'K': this.input.keyboard!.addKey(Phaser.Input.Keyboard.KeyCodes.K)
        };

        // Create UI
        this.scoreText = this.add.text(16, 16, 'Score: 0', {
            fontSize: '24px',
            color: '#ffffff'
        });

        this.streakText = this.add.text(16, 50, 'Streak: 0', {
            fontSize: '18px',
            color: '#f39c12'
        });

        // Instructions
        const instructionText = this.add.text(width / 2, 30, 'ðŸŽµ Press S-D-F-G-H-J-K to create platforms! ðŸŽµ', {
            fontSize: '18px',
            color: '#ecf0f1',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Make instructions pulse to draw attention
        this.tweens.add({
            targets: instructionText,
            alpha: 0.6,
            duration: 1500,
            yoyo: true,
            repeat: -1
        });

        // Controls info
        this.add.text(width - 16, 16, 'ESC: Menu | R: Restart', {
            fontSize: '14px',
            color: '#95a5a6'
        }).setOrigin(1, 0);

        // ESC key to return to menu
        this.input.keyboard!.on('keydown-ESC', () => {
            this.scene.start('MainMenu');
        });

        // R key to restart
        this.input.keyboard!.on('keydown-R', () => {
            this.scene.restart();
        });

        // Start the game
        this.isGameRunning = true;
        
        // Camera follows character
        this.cameras.main.startFollow(this.character, true, 0.1, 0.1);
        this.cameras.main.setDeadzone(200, 100);

        // Reset game state
        this.score = 0;
        this.streak = 0;
        this.characterSpeed = 120;
    }

    update(): void {
        if (!this.isGameRunning) return;

        // Auto-run character forward
        const characterBody = this.character.body as Phaser.Physics.Arcade.Body;
        characterBody.setVelocityX(this.characterSpeed);

        // Check for musical key presses
        Object.keys(this.gameKeys).forEach(key => {
            if (Phaser.Input.Keyboard.JustDown(this.gameKeys[key])) {
                this.playNote(key);
                this.createPlatform();
                this.updateScore(10);
            }
        });

        // Increase difficulty over time
        this.characterSpeed = Math.min(200, 120 + (this.score / 100));

        // Game over condition
        if (this.character.y > this.cameras.main.height + 100) {
            this.gameOver();
        }

        // Clean up old platforms
        this.platforms.children.entries.forEach((platform: any) => {
            if (platform.x < this.character.x - 400) {
                platform.destroy();
            }
        });
    }

    private playNote(key: string): void {
        console.log(`Playing note ${key} at ${this.noteFrequencies[key]}Hz`);
        
        // Visual feedback for key press
        this.character.setTint(0xf39c12);
        this.time.delayedCall(150, () => {
            this.character.clearTint();
        });

        // Create note indicator
        const noteText = this.add.text(this.character.x, this.character.y - 60, key, {
            fontSize: '28px',
            color: '#f39c12',
            fontFamily: 'Arial, sans-serif'
        }).setOrigin(0.5);

        // Animate note indicator
        this.tweens.add({
            targets: noteText,
            y: noteText.y - 40,
            alpha: 0,
            duration: 600,
            ease: 'Power2',
            onComplete: () => noteText.destroy()
        });

        // Screen flash effect for good hits
        const flash = this.add.rectangle(
            this.cameras.main.scrollX + this.cameras.main.width / 2,
            this.cameras.main.scrollY + this.cameras.main.height / 2,
            this.cameras.main.width,
            this.cameras.main.height,
            0xffffff,
            0.1
        );
        flash.setScrollFactor(0);
        
        this.tweens.add({
            targets: flash,
            alpha: 0,
            duration: 100,
            onComplete: () => flash.destroy()
        });
    }

    private createPlatform(): void {
        const { height } = this.cameras.main;
        
        // Create platform ahead of character with some randomness
        const platformX = this.character.x + 120 + Math.random() * 80;
        const platformY = height - 80 - Math.random() * 150;
        const platformWidth = 80 + Math.random() * 40;
        
        const platform = this.add.rectangle(platformX, platformY, platformWidth, 20, 0x2ecc71);
        this.physics.add.existing(platform, true);
        this.platforms.add(platform);

        // Platform creation effect
        platform.setAlpha(0);
        platform.setScale(0.1);
        this.tweens.add({
            targets: platform,
            alpha: 1,
            scaleX: 1,
            scaleY: 1,
            duration: 200,
            ease: 'Back.easeOut'
        });

        // Simple visual effect instead of particles
        const effect = this.add.circle(platformX, platformY, 20, 0x2ecc71, 0.5);
        this.tweens.add({
            targets: effect,
            scaleX: 2,
            scaleY: 2,
            alpha: 0,
            duration: 300,
            ease: 'Power2',
            onComplete: () => effect.destroy()
        });
    }

    private updateScore(points: number): void {
        this.score += points * (1 + this.streak * 0.1);
        this.streak++;
        
        this.scoreText.setText(`Score: ${Math.floor(this.score)}`);
        this.streakText.setText(`Streak: ${this.streak}`);
        
        // Streak visual feedback
        if (this.streak > 0 && this.streak % 5 === 0) {
            const streakBonus = this.add.text(this.character.x, this.character.y - 80, `${this.streak}x STREAK!`, {
                fontSize: '20px',
                color: '#e74c3c',
                fontFamily: 'Arial, sans-serif'
            }).setOrigin(0.5);

            this.tweens.add({
                targets: streakBonus,
                y: streakBonus.y - 40,
                alpha: 0,
                scale: 1.5,
                duration: 1200,
                ease: 'Power2',
                onComplete: () => streakBonus.destroy()
            });
        }
    }

    private gameOver(): void {
        this.isGameRunning = false;
        this.streak = 0;
        
        const { width, height } = this.cameras.main;
        
        // Stop camera follow
        this.cameras.main.stopFollow();
        
        // Game over screen
        const gameOverBg = this.add.rectangle(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2,
            width,
            height,
            0x000000,
            0.8
        );
        gameOverBg.setScrollFactor(0);
        
        const gameOverTitle = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 80,
            'Game Over!',
            {
                fontSize: '48px',
                color: '#e74c3c',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        const finalScore = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 - 20,
            `Final Score: ${Math.floor(this.score)}`,
            {
                fontSize: '24px',
                color: '#ffffff',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        const restartText = this.add.text(
            this.cameras.main.scrollX + width / 2,
            this.cameras.main.scrollY + height / 2 + 30,
            'Press R to restart or ESC for menu',
            {
                fontSize: '18px',
                color: '#bdc3c7',
                fontFamily: 'Arial, sans-serif'
            }
        ).setOrigin(0.5).setScrollFactor(0);

        // Pulsing restart text
        this.tweens.add({
            targets: restartText,
            alpha: 0.5,
            duration: 1000,
            yoyo: true,
            repeat: -1
        });

        // Send score to parent (Reddit)
        if (window.parent) {
            window.parent.postMessage({
                type: 'GAME_OVER',
                data: { finalScore: Math.floor(this.score) }
            }, '*');
        }
    }
}